import React, { Component } from 'react';
import './App.css';
import ListItems from './ListItems'

class App extends Component {
  constructor(props) {
    // super calls the base class
    super(props)
    this.state={
      items:[],
      currentItem:{
        text:'',
        key:''
      }
    }
    // this.handleInput = this.handleInput.bind(this)
    // this.addItem = this.addItem.bind(this)
    // this.deleteItem = this.deleteItem.bind(this)
    // this.setUpdate = this.setUpdate.bind(this)
  }

  handleInput = (e) => {
    this.setState({
      currentItem:{
        text: e.target.value,
        key: Date.now()
      }
    })
  }

  addItem = (e) => {
    e.preventDefault();
    const newItem = this.state.currentItem;
    console.log(newItem)
    if(newItem.text !==''){
      const newItems=[...this.state.items, newItem]
      this.setState({
        items: newItems,
        currentItem: {
          text:'',
          key:''
        }
      })
    }
  }

  deleteItem = (key) => {
    const filteredItems = this.state.items.filter(item => item.key!==key)
    this.setState({
      items: filteredItems
    })
  }

  setUpdate = (text, key) => {
    const items = this.state.items
    items.map(item => {
      item.key === key ? item.text=text : console.log(`${item} not valid`)
    })
    this.setState({
      items: items
    })
    console.log('setUpdate is firing!')
  }

  render () {
    return (
      <div className='App'>
        <header>
          <form id='to-do-form' onSubmit={this.addItem}>
            <input type='text' placeholder='Enter Text' value={this.state.currentItem.text} onChange={this.handleInput}/>
            <button type="submit">Add</button>
          </form>
        </header>
        <ListItems 
        items={this.state.items}
        deleteItem={this.deleteItem}
        setUpdate={this.setUpdate} />
      </div>
    );
  }
}

export default App;
